/*! .footer */
.footer {
  height: auto;
  background: $green; }

.footer_container {
  margin: 0 149px;
  height: 100%; }

.footer__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  //justify-content: center
  justify-content: space-around;
  height: 100%; }

.footer__content-phones {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: space-between;
  margin: 36px 0; }

.footer__content-title {
  margin: 20px 0;
  h3 {
    font-style: normal;
    font-weight: bold;
    font-size: 34px;
    line-height: 40px;
    text-align: center;
    color: #FFFFFF; } }

.footer-phones {
  display: flex;
  display: flex;
  width: 75%;
  justify-content: space-around; }
.footer-phones-left {
  transition: all linear .5s;
  a {
    transition: all linear .5s;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 16px;
    color: #FFFFFF;
    margin: 0 14px;
    &:hover {
      text-decoration: none; } } }
.footer-text {
  p {
    font-style: normal;
    font-weight: normal;
    font-size: 12.003px;
    line-height: 14px;
    color: #FFFFFF; } }

.footer-phones-right {
  p {
    margin-bottom: 9px;
    a {
      transition: all linear .5s;
      font-style: normal;
      font-weight: normal;
      font-size: 12.003px;
      line-height: 14px;
      color: #FFFFFF;
      &:hover {
        text-decoration: none; } } } }

.footer-phones-right {
  display: flex;
  flex-direction: column;
  align-items: center; }
/*! .footer end*/
