/*! Defaults */
body {
  color: black;
  font-family: Roboto;
  font-size: 1rem;
}

button, html input[type="button"], input[type="reset"], input[type="submit"] {
  -webkit-appearance: none;
  cursor: pointer;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.content {
  flex: 1 0 auto;
  background: #ffffff;
  height: 100%;
}

.header__content-mobile {
  z-index: 9999;
}

.footer {
  flex: 0 0 auto;
}

img {
  vertical-align: middle;
}

/*! Defaults End */
/*! .header */
.header {
  height: 100px;
  padding: 14px 0;
  background-color: #fff;
  transition: all linear .5s;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 999;
}

.header-mobile {
  height: 82px;
  padding: 20px 0;
  transition: all linear .5s;
  background: #ffffff;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  transition: all linear .5s;
}

.header__container {
  margin: 0 139px;
  transition: all linear .5s;
}

.header__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  transition: all linear .5s;
}

.mobile-soc {
  display: flex;
  flex-direction: row !important;
}

.mobile-soc a {
  margin: 0 4px !important;
}

.header__content-menu nav {
  display: flex;
}

.header__content-menu nav li {
  list-style-type: none;
  margin: 0 10px;
}

.header__content-menu nav li a {
  transition: all linear .5s;
  text-decoration: none;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 21px;
  color: #000000;
}

.header__content-menu nav li a:hover {
  text-decoration: none;
  color: #64C636;
}

.header__content-phone {
  display: flex;
  align-items: center;
}

.phones li {
  list-style-type: none;
  transition: all linear .5s;
}

.header__content-soc {
  display: flex;
  flex-direction: column;
  margin: 0 10px;
}

.header__content-soc a {
  margin: 3.5px 0;
}

.phones li a {
  transition: all linear .5s;
  text-decoration: none;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 23px;
  color: #000000;
  margin: 3.5px 0;
}

.phones li a:hover {
  color: #64C636;
}

.header__mob-menu span {
  position: relative;
  display: block;
  width: 20px;
  height: 2px;
  background: #6EC629;
  transition: all .2s ease-in-out;
}

.header__mob-menu span:before, .header__mob-menu span:after {
  position: absolute;
  background: #6EC629;
  content: '';
  width: 20px;
  height: 2px;
  transition: all .2s ease-in-out;
}

.header__mob-menu span:before {
  top: -7px;
}

.header__mob-menu span:after {
  top: 8px;
}

.header__content-mobile {
  z-index: 99999;
}

.header__mob-menu a.active span {
  background: transparent;
}

.header__mob-menu a.active span:before {
  transform: rotate(45deg) translate(5px, 6px);
}

.header__mob-menu a.active span:after {
  transform: rotate(-45deg) translate(5px, -6px);
}

.header-mobile {
  display: none;
}

.header__content-mobile {
  transition: all linear .5s;
  display: flex;
  justify-content: space-around;
  background: #fff;
  padding: 20px 0;
  position: absolute;
  top: -281px;
  left: 0;
  right: 0;
}

.header__content-mobile-soc {
  display: flex;
  flex-direction: column-reverse;
  align-items: flex-end;
  justify-content: space-between;
}

.header__content-mobile-text {
  transition: all linear .5s;
}

.header__content-mobile-text nav li {
  list-style-type: none;
  padding: 8px 0;
}

.header__content-mobile-text nav li a {
  transition: all linear .5s;
  font-style: normal;
  font-weight: 500;
  font-size: 17px;
  line-height: 22px;
  color: #000000;
}

.header__content-mobile-text nav li a:hover {
  text-decoration: none;
  color: #6EC629;
}

